<template>
  <el-dialog @close="resetInfo" :title="formData.id===0?'新增':'编辑'" :visible.sync="info.isShow">
    <el-form ref="roleForm" label-width="100px" :model="formData" :rules="rules">
      <el-form-item label="角色名称" prop="rolename">
        <el-input placeholder="请输入角色名称" v-model="formData.rolename"></el-input>
      </el-form-item>
      <el-form-item label="角色权限" prop="menus">
        <el-tree
          ref="tree"
          :data="menuList"
          show-checkbox
          node-key="id"
          :default-checked-keys="[]"
          :props="defaultProps"
          default-expand-all
        ></el-tree>
      </el-form-item>
      <el-form-item label="状态" prop="status">
        <el-radio-group v-model="formData.status">
          <el-radio :label="1">正常</el-radio>
          <el-radio :label="2">禁用</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="info.isShow = false">取 消</el-button>
      <el-button type="primary" @click="submitForm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"
export default {
  props: ["info"],
  computed: {
    ...mapState({ menuList: state => state.menu.menuList })
  },
  methods: {
    ...mapActions("menu", ["getMenuListAction"]),
    ...mapActions("role", ["getRoleListAction"]),
    submitForm() {
      this.$refs.roleForm.validate(valid => {
        if (valid) {
          // 提交表单 [1,2,4]=>1,2,4
          const url = this.formData.id > 0 ? "/roleedit" : "/roleadd"
          this.formData.menus = this.$refs.tree.getCheckedKeys().join(",")
          this.$http.post(url, this.formData).then(res => {
            if (res.code === 200) {
              this.$success(res.msg)
              // 更新角色列表
              this.getRoleListAction()
              // 重置表单信息&隐藏模态框
              this.resetInfo()
            }else{
              this.$error(res.msg)
            }
          })
        }
      })
    },
    // 重新信息
    resetInfo() {
      // 重新表单元素
      this.formData = { id: 0, rolename: "", menus: "", status: "" }
      // 隐藏模态框
      this.info.isShow = false
      // 清空树形控件已勾选的节点
      this.$refs.tree.setCheckedKeys([])
    },
    // 设置表单内容
    setInfo(obj) {
      this.formData = { ...obj }
      // 设置树形控件默认的勾选节点s
      // setTimeout(()=>{
      //   this.$refs.tree.setCheckedKeys(this.formData.menus.split(','))
      // },500)
      // $nextTick 将某个动作延迟到dom渲染完毕之后再执行
      this.$nextTick(() => {
        this.$refs.tree.setCheckedKeys(this.formData.menus.split(","))
      })
    }
  },
  created() {
    this.getMenuListAction()
  },
  data() {
    // rule: 系统自动注入的表单校验规则对象
    // value: 和当前数据进行绑定的表单元素的值
    // callback: 系统提供的回调函数
    var checkMenus = (rule, value, callback) => {
      // 当树形控件没有勾选任何菜单节点, 表单校验不通过
      if (this.$refs.tree.getCheckedKeys().length === 0) {
        return callback(new Error("请选择菜单权限"))
      }
      callback()
    }

    return {
      // 和表单元素进行双向数据绑定
      formData: {
        id: 0,
        rolename: "", // 角色名称
        menus: "", // 权限  1,2,3,4
        status: "" // 状态1:正常 2:禁用
      },
      // 表单校验规则
      rules: {
        rolename: {
          required: true,
          message: "请输入角色名称",
          trigger: "blur"
        },
        menus: { validator: checkMenus, trigger: "blur" },
        status: { required: true, message: "请选择状态", trigger: "blur" }
      },
      defaultProps: {
        // 自定义子节点存储的属性名
        children: "children",
        // label: 自定义显示文本存储的属性名
        label: "title"
      }
    }
  }
}
</script>

<style>
</style>


