<template>
  <el-card>
    <el-button @click="info.isShow=true" type="primary" icon="el-icon-circle-plus-outline">新增</el-button>
    <el-divider />
    <el-table :data="roleList" style="width: 100%">
      <el-table-column prop="id" label="ID" min-width="80"></el-table-column>
      <el-table-column prop="rolename" label="角色名称" min-width="180"></el-table-column>
      <el-table-column prop="status" label="状态" min-width="180">
          <template v-slot="$props">
              <el-tag type="success" v-if="$props.row.status===1">正常</el-tag>
              <el-tag type="danger" v-else>禁用</el-tag>
          </template>
      </el-table-column>
      <el-table-column label="操作" min-width="180">
          <!--   -->
           <template v-slot="$props">
              <el-button @click="edit($props.row)" type="success" icon="el-icon-edit" circle size="mini"></el-button>
              <el-button @click="del($props.row.id)" type="danger" icon="el-icon-delete" circle size="mini"></el-button>
          </template>
      </el-table-column>
    </el-table>
    <!-- 添加表单组件 -->
    <Dialog :info="info" ref="dialog"/>
  </el-card>
</template>

<script>
import Dialog from './Dialog'
import { mapState, mapActions } from "vuex";
export default {
  components:{Dialog},  
  computed: {
     ...mapState({roleList:state=>state.role.roleList}) 
  },
  data() {
    return {
        // 即将传递给子组件的数据
        info:{
            // 控制表单所在模态框的显示状态
            isShow:false
        }
    }
  },
  methods:{
      ...mapActions('role',['getRoleListAction']),
      // 编辑
      edit(row){
        // 显示模态框组件
        this.info.isShow=true
        this.$refs.dialog.setInfo(row)
      },
      // 删除
      del(id){
          this.$myConfirm(()=>{
            this.$http.post('/roledelete',{id}).then(res=>{
              if(res.code===200){
                // 1-成功提示
                this.$success(res.msg)
                // 2-更新角色列表
                this.getRoleListAction()
              }else{
                this.$error(res.msg)
              }
            })
          })
      }
  },
  created(){
      this.getRoleListAction()
  }
};
</script>

<style>
</style>


